@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
body {
  margin: 0;
  box-sizing: border-box; }

.header {
  width: 1440px;
  height: 70px;
  background: #FFFFFF;
  box-shadow: 0px 10px 20px rgba(31, 32, 65, 0.05); }

.logo__container {
  position: absolute;
  width: 105.69px;
  height: 40px;
  left: 143px;
  top: 18px;
  margin-top: -3px;
  margin-left: -3px; }

.navmenu {
  position: relative;
  width: 480px;
  height: 21px;
  left: 527px;
  top: 30px; }

#leftpetal {
  position: relative;
  left: -31px;
  top: -12px; }

#rightpetal {
  position: relative;
  left: -32px;
  top: -18px;
  fill: -webkit-linear-gradient(180deg, #6FCF97 0%, #66D2EA 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; }

#toxin {
  position: relative;
  left: 52px;
  top: -31px; }

nav {
  position: relative;
  width: 480px;
  height: 21px;
  left: 500px;
  top: 23px; }

ul {
  list-style: none;
  margin: 0;
  padding: 0; }

.topmenu > li {
  display: inline-block;
  position: relative;
  top: -14.1px;
  left: -1px; }

.topmenu > li:last-child {
  margin-right: 0; }

a {
  display: block;
  padding: 0px 5px;
  text-decoration: none;
  outline: none;
  font-family: 'Montserrat', sans-serif;
  font-weight: normal;
  font-size: 14px;
  margin-right: 11px;
  letter-spacing: -0.03px; }

.submenu {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  z-index: 10;
  transition: 0.5s ease-in-out; }

.navmenu ul {
  margin-bottom: none; }

.navmenu ul a {
  color: rgba(31, 32, 65, 0.5); }

.navmenu .submenu {
  display: none; }

.navmenu ul li:hover .submenu {
  display: block;
  margin-top: 15px; }

.navmenu ul li:hover .submenu li {
  display: block;
  margin-bottom: 15px; }

.material-icons {
  position: relative;
  width: 24px;
  height: 21px;
  top: 8px;
  left: 1px;
  font-family: Material Icons;
  font-style: normal;
  font-weight: normal; }

.top-button-1 {
  position: relative;
  width: 87px;
  height: 34px;
  left: 997px;
  top: -3px;
  background-color: white;
  border-radius: 22px;
  border-color: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);
  border: 2px solid #BC9CFF; }

.top-button-1:hover {
  color: #9555af; }

.top-button-1 a {
  color: #BC9CFF;
  margin: 0;
  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 11px;
  line-height: 15px;
  /* identical to box height */
  text-transform: uppercase;
  letter-spacing: 1px;
  /* Primary Color */ }

.top-button-2 {
  position: relative;
  width: 196px;
  height: 34px;
  left: 1017px;
  top: -3px;
  /* Primary Gradient */
  background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);
  border-radius: 22px;
  border-color: #BC9CFF;
  border: 1px solid #BC9CFF; }

.top-button-2 a {
  color: white;
  margin: 0;
  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 11px;
  line-height: 15px;
  letter-spacing: 0.2px;
  /* identical to box height */
  letter-spacing: 1px;
  text-transform: uppercase;
  /* Primary Color */ }

.containerSlide {
  width: 1440px;
  padding-top: 0px; }

.containerSlide img {
  height: 830px; }

.footer {
  width: 1440px;
  height: 340px;
  left: 0px;
  top: 900px;
  background: #FFFFFF;
  box-shadow: 0px 1px 0px rgba(31, 32, 65, 0.1); }

.container {
  position: relative;
  padding: 69px 140px;
  width: 1440px;
  height: 372px;
  padding-bottom: 0px; }

.bottom_logo__box {
  position: relative;
  width: 105.69px;
  height: 44px; }

.logo_adv {
  width: 260px;
  height: 72px;
